import { State } from "../../../../model/State";
import { DrawLayer } from "./DrawLayer";
class TemporaryLayer extends DrawLayer {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor(state, name) {
        super(state, name);
        //=============================================
        // event handler
        //=============================================
        //=============================================
        // private
        //=============================================
        this._superSetDrawLayerData = this.setDrawLayerData;
        //=============================================
        // public
        //=============================================
        this.init = () => {
            this.x = 0;
            this.y = 0;
            this.visible = false;
            this.graphics.clear();
            this.updateCache();
        };
        this.setStageMargin = (value) => {
            this._stageMargin = value;
        };
        this.setDrawLayerData = (dld) => {
            this._superSetDrawLayerData(dld);
            this._width = dld.width * this._dotSize;
            this._height = dld.height * this._dotSize;
        };
        this.getDrawLayerData = () => {
            let cc = this.cacheCanvas;
            let ctx = cc.getContext("2d");
            /*
            console.log(
                this._stageWidth,
                this._stageHeight,
                this._dotSize,
                this._drawAreaLeft,
                this._drawAreaTop,
                this._drawAreaRight,
                this._drawAreaBottom
            );
            */
            this._c2ld.init(ctx, this._stageWidth, this._stageHeight, this._dotSize, false, true, this._drawAreaLeft, this._drawAreaTop, this._drawAreaRight, this._drawAreaBottom);
            let dld = this._c2ld.getDrawLayerData(1);
            dld.x = dld.x + Math.floor(this.x / this._dotSize);
            dld.y = dld.y + Math.floor(this.y / this._dotSize);
            this._c2ld.dispose();
            return dld;
        };
        this.move = (mouseX, mouseY) => {
            if (this._state.current == State.SELECT_COPY || this._state.current == State.SELECT_CUT) {
                this.alpha = 0.5;
                let xx = mouseX - this._width / 2;
                let yy = mouseY - this._height / 2;
                this.x = this._adustX(xx - this._drawAreaLeft) + (this._drawAreaLeft - this._stageMargin);
                this.y = this._adustY(yy - this._drawAreaTop) + (this._drawAreaTop - this._stageMargin);
                //console.log(mouseX, xx, this._drawAreaLeft, this._stageMargin, this.x);
                //this.x= this._adustX(xx) - this._drawAreaLeft + this._stageMargin;
                //this.y= this._adustY(yy) - this._drawAreaTop + this._stageMargin;
                this.dispatchEvent(new createjs.Event(TemporaryLayer.EVENT_MOVE_TEMP, true, true));
            }
        };
    }
    //=============================================
    // getter/setter
    //=============================================
    get drawAreaLeft() {
        return this._drawAreaLeft;
    }
    get drawAreaTop() {
        return this._drawAreaTop;
    }
}
