// TODO
//=============================================
/*
    Canvasの描画領域の大きさが変わると、getDrawLayerDataの戻り値の座標計算がずれる…
*/
//=============================================
// 定数/変数
//=============================================
//----------public----------
TemporaryLayer.EVENT_MOVE_TEMP = "event move temporary layer";
export { TemporaryLayer };
