LayerPanelRow.EVENT_MOUSEDOWN_LAYERPANELROW = "event mouse down layer panel row";
LayerPanelRow.EVENT_CHANGE_LAYERPANELROW = "event change layer panel row";
export { LayerPanelRow };
class Eye extends createjs.EventDispatcher {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor(target) {
        super();
        //=============================================
        // event handler
        //=============================================
        this._onClickHandler = (e) => {
            if (this._visible) {
                this._target.classList.add("hide");
                this._visible = false;
            }
            else {
                this._target.classList.remove("hide");
                this._visible = true;
            }
            this.dispatchEvent(new createjs.Event(Eye.EVENT_CHANGE_EYE, true, true));
        };
        this._target = target;
        this._visible = true;
        this._target.addEventListener("click", this._onClickHandler);
    }
    //=============================================
    // private
    //=============================================
    //=============================================
    // public
    //=============================================
    //=============================================
    // getter/setter
    //=============================================
    get visible() {
        return this._visible;
    }
    set visible(value) {
        this._visible = value;
    }
}
