class Preview extends createjs.Shape {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor(stageWidth, stageHeight, dotSize) {
        super();
        //=============================================
        // event handler
        //=============================================
        //=============================================
        // private
        //=============================================
        this._drawDld = (dld, sx, sy) => {
            for (var i = 0; i < dld.dataList.length; i++) {
                let hexColorCode = '';
                let colorId = dld.dataList[i];
                if (0 < colorId) {
                    if (Number.isInteger(colorId) == true) {
                        let xx = i % dld.width;
                        let yy = Math.floor(i / dld.width);
                        hexColorCode = dld.hexColorCodeList[colorId];
                        this.graphics.beginFill('#' + hexColorCode);
                        this.graphics.drawRect((xx + dld.x) * this._dotSize + sx, (yy + dld.y) * this._dotSize + sy, this._dotSize, this._dotSize);
                        //console.log((xx + dld.x) * this._dotSize + sx, (yy + dld.y) * this._dotSize + sy, this._dotSize, this._dotSize);
                    }
                }
            }
        };
        this._drawBg = () => {
            this.graphics.beginFill('#' + "FFFFFF");
            //this.graphics.beginFill('#' + "AAAAAA");
            this.graphics.drawRect(0, 0, this._stageWidth, this._stageHeight);
        };
        //=============================================
        // public
        //=============================================
        this.drawPad = (pad, isBg) => {
            this._pad = pad;
            //console.log(pad);
            //console.log(pad.width,pad.x,this._stageWidth,this._dotSize);
            let sx = Math.floor((this._stageWidth - pad.width * this._dotSize) / 2 - (pad.x * this._dotSize));
            let sy = Math.floor((this._stageHeight - pad.height * this._dotSize) / 2 - (pad.y * this._dotSize));
            let sw = Math.floor((pad.width + pad.x) * this._dotSize);
            let sh = Math.floor((pad.height + pad.y) * this._dotSize);
            let dldList = pad.getDrawLayerDataList();
            let dld;
            this.cache(sx, sy, sw, sh);
            if (isBg) {
                this._drawBg();
            }
            for (let key in dldList) {
                dld = dldList[key];
                this._drawDld(dld, sx, sy);
            }
            this.updateCache();
        };
        this.drawDld = (dld, sx, sy, sw, sh, isBg) => {
            this.cache(sx, sy, sw, sh);
            if (isBg) {
                this._drawBg();
            }
            this._drawDld(dld, sx, sy);
            this.updateCache();
        };
        this.addMouseEvent = () => {
            this.addEventListener("mouseover", (e) => {
                //参考：http://www.fumiononaka.com/Business/html5/FN1211002.html
                document.body.style.cursor = "pointer";
                this.alpha = 0.5;
                this.stage.update();
            });
            this.addEventListener("mouseout", (e) => {
                document.body.style.cursor = "";
                this.alpha = 1;
                this.stage.update();
            });
            this.addEventListener("click", (e) => {
                this.dispatchEvent(new createjs.Event(Preview.EVENT_CLICK_PREVIEW, true, true));
            });
        };
        this.removeMouseEvent = () => {
            this.removeAllEventListeners();
        };
        this._stageWidth = stageWidth;
        this._stageHeight = stageHeight;
        this._dotSize = dotSize;
    }
    //=============================================
    // getter/setter
    //=============================================
    get pixcelArtData() {
        return this._pad;
    }
    set dotSize(value) {
        this._dotSize = value;
    }
}
