class KeyBindManager {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor() {
        //=============================================
        // event handler
        //=============================================
        //=============================================
        // private
        //=============================================
        //=============================================
        // public
        //=============================================
        this.add = (key, target, methodType) => {
            this._list[key] = new KeyBindManagerData(key, target, methodType);
        };
        this._list = {};
        /*
        document.addEventListener("kyepress", (e:KeyboardEvent) => {
            //console.log(e, e.key);
        });
        */
        document.addEventListener("keydown", (e) => {
            if (e.key != this._currentKey) {
                let data = this._list[e.key];
                if (data) {
                    data.execute();
                }
                this._currentKey = e.key;
            }
            return false;
        });
    }
}
