//=============================================
// 定数/変数
//=============================================
//----------public----------
KeyBindManager.METHOD_TYPE_CLICK = "method type click";
export { KeyBindManager };
class KeyBindManagerData {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor(key, target, methodType) {
        //=============================================
        // public
        //=============================================
        this.execute = () => {
            if (this._methodType == KeyBindManager.METHOD_TYPE_CLICK) {
                this._target.click();
            }
        };
        this._key = key;
        this._target = target;
        this._methodType = methodType;
    }
}
