import { DrawLayerData } from "./DrawLayerData";
export class PixcelArtData {
    //=============================================
    // constructor
    //=============================================
    constructor(isBlank = false) {
        //=============================================
        // private
        //=============================================
        //=============================================
        // public
        //=============================================
        this.setValue = (dotJsonObj, id = null, title = null) => {
            this._id = id;
            this._title = title;
            this._drawLayerDataList = [];
            if (dotJsonObj != null) {
                this._dotJsonObj = dotJsonObj;
                for (var key in this._dotJsonObj) {
                    let layerJsonObj = this._dotJsonObj[key];
                    let dld = new DrawLayerData();
                    dld.name = key;
                    dld.setJsonObj(layerJsonObj);
                    this._drawLayerDataList.push(dld);
                }
            }
            this.layoutInit();
        };
        this.getJsonObj = () => {
            let obj = {};
            for (var i = 0; i < this._drawLayerDataList.length; i++) {
                let dld = this._drawLayerDataList[i];
                obj[dld.name] = dld.getJsonObj();
            }
            let result = { "id": this._id, "title": this._title, "dot_json": obj };
            //console.log('\n[JsonObj]', "\n\t", result);
            //console.log('\n[JsonStr]', "\n\t", JSON.stringify(result));
            return result;
        };
        //色配列を返す
        this.getHexColorCodeList = () => {
            let result = [];
            for (var i = 0; i < this._drawLayerDataList.length; i++) {
                let dld = this._drawLayerDataList[i];
                let hexColorCodeList = dld.hexColorCodeList;
                result = result.concat(hexColorCodeList);
            }
            //重複削除
            result = Array.from(new Set(result));
            //最初の１つを削除
            result.shift();
            return result;
        };
        this.addDrawLayerData = (dld) => {
            this._drawLayerDataList.push(dld);
        };
        this.getDrawLayerDataList = () => {
            return this._drawLayerDataList;
        };
        this.clearDrawLayerDataList = () => {
            this._drawLayerDataList = [];
        };
        this.layoutInit = () => {
            //console.log(this._dotJsonObj);
            /*
            for (var key in this._dotJsonObj) {
                let dot_json: any = this._dotJsonObj[key];
                let layerData: DrawLayerData = new DrawLayerData();
                layerData.name = key;
                layerData.setJsonObj(dot_json);
                this._drawLayerDataList.push(layerData);
            }
            */
            let minX = Number.MAX_SAFE_INTEGER;
            let minY = Number.MAX_SAFE_INTEGER;
            let maxX = 0;
            let maxY = 0;
            //console.log(this._drawLayerDataList);
            for (var i = 0; i < this._drawLayerDataList.length; i++) {
                let dld = this._drawLayerDataList[i];
                if (dld.x < minX) {
                    minX = dld.x;
                }
                if (dld.y < minY) {
                    minY = dld.y;
                }
                if (maxX < dld.x + dld.width) {
                    maxX = dld.x + dld.width;
                }
                if (maxY < dld.y + dld.height) {
                    maxY = dld.y + dld.height;
                }
            }
            this._x = minX;
            this._y = minY;
            this._width = maxX - minX;
            this._height = maxY - minY;
        };
        this._drawLayerDataList = [];
        if (isBlank) {
            this.addDrawLayerData(new DrawLayerData("body"));
        }
    }
    //=============================================
    // getter/setter
    //=============================================
    get id() {
        return this._id;
    }
    set id(value) {
        this._id = value;
    }
    get title() {
        return this._title;
    }
    set title(value) {
        this._title = value;
    }
    get x() {
        return this._x;
    }
    set x(value) {
        this._x = value;
    }
    get y() {
        return this._y;
    }
    set y(value) {
        this._y = value;
    }
    get width() {
        return this._width;
    }
    get height() {
        return this._height;
    }
}
