import { State } from "../model/State";
//import Sortable from "@type/sortablejs";
import Sortable from "sortablejs";
import { LayerPanelRow } from "../view/ui/LayerPanelRow";
import { DrawLayerData } from "../model/data/DrawLayerData";
class LayerPanelController extends createjs.EventDispatcher {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor(state) {
        super();
        //=============================================
        // event handler
        //=============================================
        this._onClickAddLPRHandler = (e) => {
            let lpr;
            for (var i = 0; i < this._lprList.length; i++) {
                lpr = this._lprList[i];
                if (!lpr.isShow) {
                    let dld = new DrawLayerData(String(new Date().getTime()));
                    lpr.setDld(dld);
                    lpr.show();
                    //this._activeLayerId = i;
                    break;
                }
            }
            this._state.setCurrent(State.LAYER_CHANGE);
            this.changedState();
            this.dispatchEvent(new createjs.Event(LayerPanelController.EVENT_CHANGE_DATA_LAYERPANEL, true, true));
        };
        this._onClickDeleteLPRHandler = (e) => {
            let lpr;
            for (var i = 0; i < this._lprList.length; i++) {
                lpr = this._lprList[i];
                if (lpr.isActive) {
                    lpr.hide();
                    break;
                }
            }
            let rowLiList = document.querySelectorAll('#layerPanelBody > ul > li');
            let rowLi;
            for (var j = 0; j < rowLiList.length; j++) {
                rowLi = rowLiList[j];
                if (!rowLi.className.match(/hide/)) {
                    rowLi.dispatchEvent(new Event('mousedown')); //マウスダウンを強制的に発火してアクティブを変更
                    break;
                }
            }
            this._state.setCurrent(State.LAYER_CHANGE);
            this.changedState();
            this.dispatchEvent(new createjs.Event(LayerPanelController.EVENT_CHANGE_DATA_LAYERPANEL, true, true));
        };
        this._onMousedownLPRHandler = (e) => {
            let target = e.target;
            let lpr;
            for (var i = 0; i < this._lprList.length; i++) {
                lpr = this._lprList[i];
                if (target.txt == lpr.txt) {
                    lpr.active();
                }
                else {
                    lpr.inactive();
                }
            }
            let rowSpanList = document.querySelectorAll('#layerPanelBody > ul > li > .layerPanelRowTxt > span');
            let rowSpan;
            let rowSpanTxt;
            for (var j = rowSpanList.length - 1; 0 < j; j--) {
                rowSpan = rowSpanList[j];
                rowSpanTxt = rowSpan.innerHTML;
                if (target.txt == rowSpanTxt) {
                    let idx = this._lprList.length - 1 - j;
                    this._activeLayerId = idx;
                    break;
                }
            }
            //アクティブレイヤーを変更するだけなので、Stateを変えない
            //this._state.setCurrent(State.LAYER_CHANGE);
            console.log('\n[LayerPanel Event]', e.type, "\n\t" + "state : " + this._state.current);
            this.dispatchEvent(new createjs.Event(LayerPanelController.EVENT_CHANGE_ACTIVELAYER_LAYERPANEL, true, true));
        };
        this._onChangeLPRHandler = (e) => {
            this._state.setCurrent(State.LAYER_CHANGE);
            console.log('\n[LayerPanel Event]', e.type, "\n\t" + "state : " + this._state.current);
            this.dispatchEvent(new createjs.Event(LayerPanelController.EVENT_CHANGE_DATA_LAYERPANEL, true, true));
        };
        this._onChangeSortableHandler = (e) => {
            this._state.setCurrent(State.LAYER_CHANGE);
            console.log('\n[LayerPanel Event]', e.type, "\n\t" + "state : " + this._state.current);
            this.dispatchEvent(new createjs.Event(LayerPanelController.EVENT_CHANGE_DATA_LAYERPANEL, true, true));
        };
        //=============================================
        // private
        //=============================================
        //=============================================
        // public
        //=============================================
        this.setPad = (pad) => {
            console.log('\n[LayerPanel]', "setPad", "\n\t" + "state : " + this._state.current);
            this._pad = pad;
            let dldList = this._pad.getDrawLayerDataList();
            let dld;
            let lpr;
            for (var j = 0; j < this._lprList.length; j++) {
                lpr = this._lprList[j];
                let isMatch = false;
                let idx = this._lprList.length - 1 - j;
                for (var i = 0; i < dldList.length; i++) {
                    dld = dldList[i];
                    //console.log(i, j);
                    if (i == idx) {
                        lpr.show();
                        lpr.setDld(dld);
                        isMatch = true;
                        break;
                    }
                }
                //console.log(j, isMatch);
                if (!isMatch) {
                    lpr.hide();
                }
            }
            this.changedState();
        };
        this.getPad = () => {
            this._pad.clearDrawLayerDataList();
            let lpr;
            let dld;
            let rowSpanList = document.querySelectorAll('#layerPanelBody > ul > li > .layerPanelRowTxt > span');
            let rowSpan;
            let rowSpanTxt;
            for (var j = rowSpanList.length - 1; 0 < j; j--) {
                rowSpan = rowSpanList[j];
                rowSpanTxt = rowSpan.innerHTML;
                if (rowSpanTxt) {
                    for (var i = this._lprList.length - 1; 0 < i; i--) {
                        lpr = this._lprList[i];
                        if (lpr.isShow && (rowSpanTxt == lpr.txt)) {
                            dld = lpr.getDld();
                            this._pad.addDrawLayerData(dld);
                            break;
                        }
                    }
                }
            }
            return this._pad;
        };
        this.changedState = () => {
            let lpr;
            let showCount = 0;
            for (var i = 0; i < this._lprList.length; i++) {
                lpr = this._lprList[i];
                if (lpr.isShow) {
                    showCount++;
                }
            }
            //console.log(this._lprList.length, showCount);
            this._addLPR.classList.remove("disabled");
            this._deleteLPR.classList.remove("disabled");
            if (showCount <= 1) {
                this._deleteLPR.classList.add("disabled");
            }
            if (this._lprList.length <= showCount) {
                this._addLPR.classList.add("disabled");
            }
        };
        this._state = state;
        // List with handle
        let rows = document.querySelector('#layerPanelBody > ul');
        Sortable.create(rows, {
            handle: '.layerPanelRowHandle',
            animation: 150,
            onEnd: this._onChangeSortableHandler
        });
        this._lprList = [];
        let rowList = rows.querySelectorAll('li');
        let ele;
        let lpr;
        for (var i = 0; i < rowList.length; i++) {
            ele = rowList[i];
            lpr = new LayerPanelRow(ele);
            lpr.addEventListener(LayerPanelRow.EVENT_MOUSEDOWN_LAYERPANELROW, this._onMousedownLPRHandler);
            lpr.addEventListener(LayerPanelRow.EVENT_CHANGE_LAYERPANELROW, this._onChangeLPRHandler);
            this._lprList.push(lpr);
        }
        this._addLPR = document.querySelector('#addLayerPanelRow');
        this._deleteLPR = document.querySelector('#deleteLayerPanelRow');
        this._addLPR.addEventListener("click", this._onClickAddLPRHandler);
        this._deleteLPR.addEventListener("click", this._onClickDeleteLPRHandler);
    }
    //=============================================
    // getter/setter
    //=============================================
    get activeLayerId() {
        return this._activeLayerId;
    }
}
//=============================================
// TODO
//=============================================
//=============================================
// 定数/変数
//=============================================
//----------public----------
LayerPanelController.EVENT_CHANGE_DATA_LAYERPANEL = "event change data layer panel";
LayerPanelController.EVENT_CHANGE_ACTIVELAYER_LAYERPANEL = "event change active layer layer panel";
export { LayerPanelController };
